<template>
	<div>
		<div class="register_view">
			<el-form :model="registerForm" class="register_form">
				<div class="title_view">{{projectName}}</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.zuojiazhanghao" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.zuojiaxingming" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.mima" 
						 placeholder=""
						 type="password"
						 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp" v-model="registerForm.mima2" type="password" placeholder="" />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<div :style='{"width":"100%","margin":"0 0 0 10px","flex":"1"}' class="list_file_list">
						<uploads
							action="file/upload" 
							tip="" 
							:limit="1"
							:fileUrls="registerForm.zhaopian?registerForm.zhaopian:''" 
							@change="zhaopianUploadSuccess">
						</uploads>
					</div>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-select 
						class="list_sel"
						v-model="registerForm.xingbie" 
						placeholder=""
						>
						<el-option v-for="item in zuojiaxingbieLists" :label="item" :value="item"></el-option>
					</el-select>
				</div>

				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.dianhua" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.zuojiajianjie" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_btn">
					<el-button class="register" type="success" @click="handleRegister"></el-button>
					<div class="r-login" @click="close"></div>
				</div>
			</el-form>
		</div>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context?.$project.projectName
	//
	import { useRoute } from 'vue-router';
	const route = useRoute()
	const tableName = ref('zuojia')
	
	//
	const getUUID=()=> {
		return new Date().getTime();
	}
	const registerForm = ref({
        xingbie: '',
	})
	const zuojiaxingbieLists = ref([])
	const init=()=>{
		zuojiaxingbieLists.value = ",".split(',')
	}
    const zhaopianUploadSuccess=(fileUrls)=> {
        registerForm.value.zhaopian = fileUrls;
    }
	// 
	//
	const handleRegister = () => {
		let url = tableName.value +"/register";
		if((!registerForm.value.zuojiazhanghao)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((!registerForm.value.mima)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.mima!=registerForm.value.mima2){
			context?.$toolUtil.message('','error')
			return false
		}
		if(registerForm.value.zhaopian!=null){
			registerForm.value.zhaopian = registerForm.value.zhaopian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(registerForm.value.dianhua&&(!context?.$toolUtil.isMobile(registerForm.value.dianhua))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		
		context?.$http({
			url:url,
			method:'post',
			data:registerForm.value
		}).then(res=>{
			context?.$toolUtil.message('','success', obj=>{
				context?.$router.push({
					path: "/login"
				});
			})
		})
	}
	//
	const close = () => {
		context?.$router.push({
			path: "/login"
		});
	}
	init()
</script>
<style lang="scss" scoped>
	
	.register_view {
		background-repeat: no-repeat;
		flex-direction: column;
		background-size: 100% 100%;
		background: url(http://clfile.zggen.cn/20240301/cb59505e774a42899501d8d7f1360b75.jpg);
		display: flex;
		min-height: 100vh;
		justify-content: center;
		align-items: center;
		position: relative;
		background-position: center center;
		// 
		.register_form {
			border-radius: 0px;
			padding: 50px 80px 30px 80px;
			margin: 0 auto;
			background: url(http://clfile.zggen.cn/20240301/7ac2edfec9b84ae5be0a62f62e8af7bb.png) no-repeat center top / 100% auto,#f7f2ec;
			display: flex;
			width: 600px;
			justify-content: flex-start;
			flex-wrap: wrap;
		}
		// 
		.title_view {
			padding: 0px;
			margin: 0 auto 20px;
			color: #333;
			font-weight: 500;
			width: 80%;
			font-size: 22px;
			text-align: center;
		}
		// item
		.list_item {
			margin: 10px auto;
			display: flex;
			width: 100%;
			justify-content: flex-start;
			align-items: center;
			// label
			.list_label {
				color: #666;
				background: none;
				width: 110px;
				font-size: 14px;
				line-height: 36px;
				box-sizing: border-box;
				text-align: right;
			}
			// 
			:deep(.list_inp) {
				border: 1px solid #ddd;
				border-radius: 0px;
				padding: 0 10px;
				color: #666;
				background: #fff;
				flex: 1;
				width: 100%;
				line-height: 36px;
				box-sizing: border-box;
				height: 36px;
				//
				.el-input__wrapper{
					border: none;
					box-shadow: none;
					background: none;
					border-radius: 0;
					height: 100%;
					padding: 0;
				}
				.is-focus {
					box-shadow: none !important;
				}
			}
		}
		//
		:deep(.list_sel) {
			border: 1px solid #ddd;
			border-radius: 0px;
			padding: 0 10px;
			color: #666;
			background: #fff;
			flex: 1;
			width: 100%;
			line-height: 36px;
			box-sizing: border-box;
			//
			.select-trigger{
				height: 100%;
				.el-input{
					height: 100%;
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
			}
		}
		//
		.list_btn {
			margin: 10px auto;
			display: flex;
			width: 100%;
			align-items: center;
			flex-wrap: wrap;
			//
			.register {
					border: none;
					border-radius: 0px;
					padding: 0 24px;
					margin: 0 auto;
					color: #fff;
					background: linear-gradient(270deg, rgba(130,196,209,1) 0%, rgba(115,186,200,1) 24%, rgba(174,210,217,1) 100%);
					width: auto;
					font-size: 16px;
					height: 40px;
			}
			//
			.register:hover {
				border: none;
				border-radius: 0;
				padding: 0 24px;
				margin: 0 auto;
				background: linear-gradient(30deg, rgba(130,196,209,1) 0%, rgba(115,186,200,1) 24%, rgba(174,210,217,1) 100%);
				width: auto;
				font-size: 16px;
				height: 40px;
			}
			//
			.r-login {
				cursor: pointer;
				padding: 10px 0 0;
				color: #999;
				width: 100%;
				font-size: 14px;
				text-align: right;
			}
		}
		//
		.list_file_list  {
			//
			:deep(.el-upload__tip){
				margin: 7px 0 0;
				color: #999;
				display: flex;
				font-size: 14px;
				justify-content: flex-start;
				align-items: center;
			}
			//
			:deep(.el-upload--picture-card){
				border: 1px solid #ddd;
				cursor: pointer;
				background-color: #fff;
				border-radius: 0px;
				width: 160px;
				line-height: 70px;
				text-align: center;
				height: 60px;
				//
				.el-icon{
					color: #999;
					font-size: 32px;
				}
			}
			:deep(.el-upload-list__item) {
				border: 1px solid #ddd;
				cursor: pointer;
				background-color: #fff;
				border-radius: 0px;
				width: 160px;
				line-height: 70px;
				text-align: center;
				height: 60px;
			}
		}
	}
</style>